import * as React from "react";
import { useCallback, useEffect, useState, useRef } from "react";
import { useWallet, UseWalletProvider } from 'use-wallet';
import styled from 'styled-components';
import { ethers } from "ethers";
const axios = require('axios');
const abi = require('./abi.json');

import together from '../assets/together.png';

const contractAddress = '0x583555F899D3D8132Fb938e9676a59C02Bfab053';
const network = 'rinkeby';


const Style = styled.div`
margin: auto;
width: 88%;
font-family: "Helvetica Neue", sans-serif;
font-weight: 300;
font-variant-ligatures: normal;
font-size: 2rem;
color: grey
letter-spacing: 1px;
`

const Figure = styled.figure`
display: inline-block;
`

const Image = styled.img`
max-width: 100%;
height: auto;
width: auto; /* ie8 */

`
const Caption = styled.figcaption`
font-size: small;
`

const Header = styled.div`
  height: 60px;
`

const App = () => {
  const [provider, setProvider] = useState<ethers.providers.Web3Provider>()
  const [contract, setContract] = useState<ethers.Contract>()
  const [numMinted, setNumMinted] = useState();
  const [whitelistOn, setWhitelistOn] = useState(true);
  const [paused, setPaused] = useState(true);
  const [signatureFailed, setSignatureFailed] = useState(false)
  const [signedMessage, setSignedMessage] = useState('')

  const [word1, setWord1] = useState<string>()
  const [word2, setWord2] = useState<string>()
  const [word3, setWord3] = useState<string>()

  const inputEl1 = useRef(null);
  const inputEl2 = useRef(null);
  const inputEl3 = useRef(null);

  const wallet = useWallet()

  useEffect(() => {
    // this code will run once per page load (refresh to make it run again)
    const go = async () => {
      // this is where you should hit the api /paused endpoint and setPaused
      const response = await axios.get('https://words-api-2jt24qgora-uc.a.run.app/paused');
      setPaused(response.data.paused)
      console.log(`whitelist signatures are paused: ${response.data.paused}`)
    }
    go()
  }, [])

  useEffect(() => {
    if (wallet && wallet.ethereum) {
      // this code will run when wallet is all set. nothing async happening here for now.
      if (!provider) {
        const newProvider = new ethers.providers.Web3Provider(wallet.ethereum, network)
        setProvider(newProvider)
        console.log('provider set')
      }
    }
  }, [wallet])

  useEffect(() => {
    const go = async () => {
      if (provider) {
        // this code will run when provider is set
        if (!contract) {
          const contract = new ethers.Contract(contractAddress, abi, provider)
          setContract(contract)
          console.log('contract set')
        }
      }
    }
    go()
  }, [provider])

  useEffect(() => {
    const go = async () => {
      if (contract) {
        // this code will run when contract instance is set
        const on = await contract.functions.whitelistOn();
        setWhitelistOn(on)
        const receiver = wallet.account;
        // use axios to POST to google cloud api /verify endpoint with {receiver} in body
        const body = { receiver };
        if (on && !paused) {
          try {
            console.log('verifying')
            const response = await axios.post('https://words-api-2jt24qgora-uc.a.run.app/verify', body);
            if (response.data.signedMessage) {
              setSignatureFailed(false)
              setSignedMessage(response.data.signedMessage)
            } else {
              setSignatureFailed(true)
              setSignedMessage('')
            }
          } catch (e) {
            setSignatureFailed(true)
            setSignedMessage('')
          }
        }
        try {
          console.log('fetching number of tokens minted')
          const response = await axios.get('https://words-api-2jt24qgora-uc.a.run.app/num_minted', body);
          if (response && response.data && response.data.n) {
            setNumMinted(response.data.n)
          }
        } catch (e) { console.error(e) }
      }
    }
    go();
  }, [contract, paused])

  const handleMint = useCallback((e) => {
    // ignore this
    e.preventDefault();
    if (!contract) {
      return
    }

    // handle the click event
    const go = async () => {
      if (whitelistOn) {
        if (paused) {
          return
        }
        contract.functions.whitelistMint(word1, word2, word3, signedMessage)
      }
      else {
        // make contract call to mint, pass in (word1, word2, word3)
        contract.functions.mint(word1, word2, word3)
      }
    }
    go()
  }, [wallet, provider, contract, paused, whitelistOn, word1, word2, word3]);
  return (
    <Style>
      {<Header>
        {wallet.status === 'connected' && wallet.balance != '-1' ? (
          <div>
            <div><button onClick={() => wallet.reset()}>disconnect</button> Account: {wallet.account}</div>
          </div>
        ) : (
          <div>
            {/* we should modify this onClick to use a proper useCallback function which also does optional walletconnect instead of metamask - maybe use web3modal react package so we can use even more wallets */}
            <button onClick={() => wallet.connect('injected')}>Connect Metamask</button>
          </div>
        )}
      </Header>}
      <h1 id="3-words">3words</h1>
      <p>   mint opens noon EST Sep 10th  </p>

      <Image src={together} alt="bday" />

      <form onSubmit={handleMint}>
        <div>
          <label >Word 1:
            <input
              ref={inputEl1}
              type="text"
              name="word1"
              onChange={e => setWord1(e.target.value)}
            />
          </label>
        </div>
        <div>
          <label >Word 2:
            <input
              ref={inputEl2}
              type="text"
              name="word2"
              onChange={e => setWord2(e.target.value)}
            />
          </label>
        </div>
        <div>
          <label >Word 3:
            <input
              ref={inputEl3}
              type="text"
              name="word3"
              onChange={e => setWord3(e.target.value)}
            />
          </label>
        </div>
        <div>
          <button type='submit' disabled={!wallet || wallet.status !== 'connected' || !wallet.ethereum || wallet.balance == '-1' || !word1 || !word2 || !word3} onClick={handleMint}>Mint (0.333 ETH)</button>
        </div>
        {numMinted && <div>{numMinted} 3words have been minted out of 333.</div>}
        {whitelistOn && paused && <div>Whitelist minting is currently paused. Check back at 12pm for public minting.</div>}
        {whitelistOn && signatureFailed && <div>We were unable to verify your address.</div>}
      </form>
      <p><code>3words</code> is an NFT collection generated by its minters.</p>
      <p>As a minter you get to pick a 3 word phrase, and afterwards a text-to-image model will generate what the network associates with that prompt. </p>
      <p>When the phrase is submitted, it is given one attribute: <strong>perplexity</strong></p>
      <p>For <code>3words</code>  perplexity can be thought about as:</p>
      <blockquote>
        <p>the likelihood of that phrase ever being said before</p>
      </blockquote>
      <p>While it&#39;s incredibly easy to generate a high perplexity prompt*, <i>the higher the perplexity, the more noise-like the generated image will be, so be careful </i>. </p>
      <p>Maybe the secondary market will want noise? only time will tell but for now we know we like the pixels.</p>
      <p>You won&#39;t be able to see your image immediately. Until the image is ready, an svg file of three words and their perplexity will be generated viewable in your wallet.</p>
      <p>The images and metadata will be released in batches. </p>
      <p> only  333   <code>3words</code> will ever exist - choose wisely </p>
      <p>*just query <a href="https://what3words.com/nuns.itself.splash">what3words</a> and plug in what you get</p>
      <h4 id="examples-">examples:</h4>
      <p>Here are some threads to start getting a sense of the capacity of the model</p>
      <p><a href="https://twitter.com/dribnet/status/1424712162405085184">mega thread from Tom</a> </p>
      <p><a href="https://twitter.com/dribnet/status/1427618972506021888">thread of landscapes also from Tom</a></p>
      <p><a href="https://twitter.com/search?q=#pixelart%20%20#clip&amp;src=typed_query&amp;f=top">general aggregation of related posts</a> </p>
      <p>Be aware that generative models are often run multiple times and the artist selects the best result.</p>
      <p>3words on the otherhand, mints the first image out of the network, so choose carefully! </p>

      <h4 id="background-">technical details and attribution:</h4>

      <p>Under the hood, we&#39;re querying  Huggingface&#39;s  brilliantly simple <code>bert-large-cased</code> model and seeing how far the prompt is from what BERT thinks should follow. </p>
      <p> For the pixel generation, we're using the pixray library by dribnet <a href="https://github.com/dribnet/pixray"> found here </a>.
        Tom and our team has already worked out a compensation plan, 3% of the initial mint sale will be allocated to <code> pixray.dribnet.eth </code> as detailed in the <a href='https://github.com/dribnet/pixray/blob/master/USE'> USE</a> file of pixray. </p>

    </Style>
  )
}

export default () => (
  <UseWalletProvider>
    <App />
  </UseWalletProvider>
)
